import Vue from 'vue'
import Vuex from 'vuex'
import VuePersist from 'vuex-persist'
Vue.use(Vuex)
const vuexLocal = new VuePersist({
    storage:window.localStorage
})

var store = new Vuex.Store({
    plugins: [vuexLocal.plugin],
    // 定义数据
    state:{
        login:{
            user:'',
            code:'',
            password:'',
            show:false,
            id:'',
            headPhoto: ''
        },
        arr:[] // 添加购物车
        
        // a:6
    },
    // 类似于计算属性
    getters: {

    },
    // 里边是一些方法
    mutations:{
        login(state,val){
            state.login=val
        },
        out(state,val){
            state.login=val
        },
        headPhoto(state,val){
            state.headPhoto=val
        },
        addShop(state,val){
            state.arr.push(val)
        },
        addNum(state,val){
            // console.log(val)
            state.arr[val].num ++
        },
        minusNum(state,val){
            state.arr[val].num --
        },
        del(state,val){
            state.arr.splice(val,1)
        },
        minus(state,val){
            state.arr[val].num --
        },
        clear(state){
            state.arr.splice(0)
        }

    },
    // actions 提交的是 mutation,而不是直接变更状态。异步操作
    actions:{

    }
})



export default store



