import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'


// 路由懒加载  按需加载
// 一级路由
// const Foo = () => import('./Foo.vue')
const Shou = () => import('@/page/Shou')
const Ding = () => import('@/page/Ding')
const Sou = () => import('@/page/Sou')
const Login = () => import('@/page/login/Login')
const Reset = () => import('@/page/resetting/Reset')
const Goods = () => import('@/page/goods/Goods')
const Left = () => import('@/page/goods/Left')
const right = () => import('@/page/goods/right')
const Redact = () => import('@/page/site/Redact')
const Newly = () => import('@/page/site/Newly')
const Ssite = () => import('@/page/site/Ssite')
const Moneyout = () => import('@/page/goods/Moneyout')



const cs = () => import('@/page/cs/cs')



// 二级路由
const Take = () => import('@/page/footer/Take')
const Order = () => import('@/page/footer/Order')
const Search = () => import('@/page/footer/Search')
const My = () => import('@/page/footer/My')


const Account = () => import('@/page/user/Account')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/Ding'
    },
    {
      path: '/Account',
      component: Account
    },
    {
      path: '/Ssite',
      component: Ssite
    },
    {
      path: '/Moneyout',
      component: Moneyout
    },
    {
      path: '/Newly',
      component: Newly,
      meta:{keepAlive:true}
    },
    {
      path: '/Redact',
      component: Redact
    },
    {
      path: '/Goods',
      component: Goods,
      redirect:'/Goods/Left',
      children:[
        {
          path: '/Goods/Left',
          component: Left
        },
        {
          path: '/Goods/right',
          component: right
        },
      ]
    },
    {
      path: '/Login',
      component: Login,
      meta:{keepAlive:true}
    },
    {
      path: '/cs',
      component: cs
    },
    {
      path:'/Reset',
      component: Reset
    },
    {
      path: '/Shou',
      component: Shou,
      children: [
        {
          path: '/Shou/Take',
          component: Take
        },
        {
          path: '/Shou/Order',
          component: Order
        },
        {
          path: '/Shou/Search',
          component: Search
        },
        {
          path: '/Shou/My',
          component: My,
        }
      ]
    },
    {
      path: '/Ding',
      component: Ding
    },
    {
      path: '/Sou',
      component: Sou
    }
    
  ]
})



